<template>
  <div class="enquity">
    <h1></h1>
    <el-form :model="searchForm" ref="searchForm" :rules="rules" label-position="left" label-width="120px">
      <el-form-item label="COUNTRY" prop="country">
        <el-select v-model="searchForm.country" filterable placeholder="----" :change="modifyCity">
          <el-option v-for="(item,key) in countries" :key="key" :label="key" :value="key">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="CITY" prop="city">
        <el-select v-model="searchForm.city" filterable placeholder="----">
          <el-option v-for="item in cities" :key="item" :label="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="ZIP" prop="zip">
        <el-input v-model="searchForm.zip" placeholder="" class="table1-input" style="width: 13.25rem;"></el-input>
      </el-form-item>

      <el-form-item label="/" prop="importOrOutport">
        <el-radio-group v-model="searchForm.importOrOutport">
          <el-radio value="" label=""></el-radio>
          <el-radio value="" label=""></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="" prop="transportType">
        <el-radio-group v-model="searchForm.transportType">
          <el-radio value="" label=""></el-radio>
          <el-radio value="" label=""></el-radio>
          <el-radio value="" label=""></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="" prop="box">
        <el-select v-model="searchForm.box" clearable placeholder="----" :disabled="!isSea">
          <el-option value="20">20</el-option>
          <el-option value="40">40</el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <!-- <el-date-picker v-model="searchForm.daterange" type="daterange" range-separator="" start-placeholder=""
          end-placeholder="" format="yyyy  MM  dd " value-format="yyyy-mm-dd">
        </el-date-picker> -->
        <el-date-picker
              v-model="searchForm.daterange"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd">
            </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmitSearchForm('searchForm')" @submit.native.prevent></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'enquiry',
    data() {
      return {
        base_api_url: 'https://baojia.widesight.cn/backend/public',
        countries: [],
        cities: [],
        pickerOptions: {
          shortcuts: []
        },
        searchForm: {
          country: '',
          city: '',
          zip: '',
          transportType: '', //
          importOrOutport: '', //  
          box: '', //
          daterange: ''
        },
        rules: {
          country: [{
            required: true,
            message: ''
          }],
          city: [{
            required: true,
            message: ''
          }],
          importOrOutport: [{
            required: true,
            message: ''
          }],
          transportType: [{
            required: true,
            message: ''
          }],
        }
      }
    },
    computed: {
      isSea: function() { //
        if (this.searchForm.transportType === "") {
          return true
        }
        return false
      },
      modifyCity: function(cities) { //city 
        cities = this.cities;
        console.log(cities);
        let newArr = [];
        cities.forEach(item =>{
          if(newArr.indexOf(item) == -1){
            newArr.push(item);
          }
        });
        console.log(newArr)
        this.cities = newArr;
      }
    },
    mounted: async function() {
      let res = await axios.get(this.base_api_url + '/api/citys');
      let countries = {};
      let cities = {};
      //TODO:res this.countries
      //let countries = res.data;
      for (let item in res.data) {
        countries[res.data[item]["country"]] = [];
      }
      for (let item in res.data) {
        countries[res.data[item]["country"]].push(res.data[item]["city"]);
      }
      this.countries = countries;
    },
    methods: {
      getCityList(e) { //countries city list
        this.searchForm.city = ''
        this.cities = this.countries[this.searchForm.country]
        // for(let item in this.countries ){
        //   if(this.countries[item].country === this.searchForm.country ){
        //     this.cities = this.countries[item].city
        //   }
        // }
      },
      onSubmitSearchForm(formName) {
        this.msg = ''
        console.log(formName)
        this.$refs[formName].validate((valid) => { // 
          if (valid) {
            try {
              //   let res = await axios.post('https://940b3659ddd2.ngrok.io/api/inquiryform',{//post
              //   country: this.searchForm.country, // 
              //   city: this.searchForm.city, // 
              //   zip: this.searchForm.zip, //
              //   type_jinchukou: this.searchForm.importOrOutport, //
              //   type_traffic: this.searchForm.transportType, //
              //   box_pile: this.searchForm.box, //
              //   time: this.searchForm.daterange, //
              // })
              // console.log(this.searchForm.transportType)
              let country = this.searchForm.country
              let city = this.searchForm.city
              let zip = this.searchForm.zip
              let type_jinchukou = this.searchForm.importOrOutport
              let type_traffic = this.searchForm.transportType
              let box_pile = this.searchForm.box
              let time = this.searchForm.daterange
              axios.get(this.base_api_url + '/api/inquiryform', {
                  params: {
                    country, // 
                    city, // 
                    zip, //
                    type_jinchukou, //
                    type_traffic, //
                    box_pile, //
                    time, //
                  }
                })
                .then((res) => {
                  this.searchForm.country = res.data.country,
                    this.searchForm.city = res.data.city,
                    this.searchForm.zip = res.data.zip,
                    this.searchForm.importOrOutport = res.data.type_jinchukou,
                    this.searchForm.transportType = res.data.type_traffic,
                    this.searchForm.box = res.data.box_pile,
                    this.searchForm.daterange = res.data.time,
                    this.$router.push({
                      name: 'enquiryList',
                      query: {
                        list: res.data
                      }
                    })
                  this.$message({
                    message: '',
                    type: 'success'
                  })
                })
            } catch (e) {
              console.log(e); // axios 
              this.$message.error('')
              // this.$router.push('/enquirylist')
            }
          } else { //
            console.log('error submit!!');
            return false;
          }
        });
      }
    },
    watch: {
      'searchForm.country': 'getCityList'
    }
  }
</script>

<style scoped>

</style>



